      <div class="fade-block">
        <transition name="fade" mode="out-in">
          <div v-if="currentItem" :key="currentIndex" class="image-container">
            <img v-if="currentItem.img" :src="currentItem.img" alt="Slide" class="fade-image" />
            <video v-else-if="currentItem.video" :src="currentItem.video" class="fade-video" autoplay muted loop playsinline />
          </div>
        </transition>
        <transition name="fade" mode="out-in">
          <div v-if="currentItem" :key="'text-' + currentIndex" class="text-container">
            <p class="fade-text">{{ typedText }}</p>
          </div>
        </transition>
      </div>
    </div>
    <div class="bottom-text" v-if="showNextLink" @click="goToNext">
      <p>Ïó¨Í∏∞Î•º ÎàåÎü¨ÏÑú Ïù¥ÎèôÌïòÍ∏∞</p>
    </div>
  </div>
</template>
<script setup>
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import img1 from '../assets/nextimg/img1.jpeg'
import img2 from '../assets/nextimg/img2.jpg'
import img3 from '../assets/nextimg/img3.jpg'
import img4 from '../assets/nextimg/img4.jpeg'
import video from '../assets/nextimg/video.mp4'
import video2 from '../assets/nextimg/video2.mp4'
import video3 from '../assets/nextimg/video3.mp4'

const TYPING_SPEED = 100              // Í∏ÄÏûê ÌïòÎÇòÏî© ÌÉÄÏù¥Ìïë ÏÜçÎèÑ
const TYPING_DELAY = 800              // ÌÉÄÏù¥Ìïë ÏãúÏûë Ï†Ñ ÎîúÎ†àÏù¥
const PHASE_DISPLAY_TIME = 1500       // Î¨∏Ïû• ÏôÑÏÑ± ÌõÑ Ïú†ÏßÄ ÏãúÍ∞Ñ
const IMAGE_CHANGE_DELAY = 800        // Ïù¥ÎØ∏ÏßÄ Ï†ÑÌôò Ï†Ñ ÎîúÎ†àÏù¥

const router = useRouter()

function goToNext() {
  router.push('/Letter')
}

const items = [
  { img: img1, phases: [
    'Ïö∞Î¶¨Í∞Ä Ï≤òÏùå ÎßåÎÇ¨Îçò Í∑∏Îïå, Ïö∞Î¶¨Îäî ÏïåÏïòÏùÑÍπå?',
    'ÌíãÌíã ÌñàÎçò 20ÎåÄÎ•º ÏßÄÎÇò, 10ÎÖÑÏù¥ÎùºÎäî Í∏∞Í∞ÑÏùÑ',
    'Ìï®ÍªòÌïòÎ©∞ ÏóÜÏñ¥ÏÑúÎäî ÏïàÎê† Ï°¥Ïû¨Í∞Ä Îê† Ï§ÑÏùÑ ÎßêÏù¥Ïïº.'
  ] },
  { img: img2, phases: [
    '10ÎÖÑÏù¥ÎùºÎäî Í∏∞Í∞Ñ ÎèôÏïà Î™®Îì† ÎÇ†, Î™®Îì† Ïùº',
    'ÏÇ¨ÏÜåÌïú ÏùºÏÉÅÎèÑ ÎöúÎãàÏôÄ Ìï®ÍªòÎùºÎ©¥ ÌäπÎ≥ÑÌñàÏñ¥',
    'ÎöúÎãàÎèÑ Í∑∏Î†áÏßÄ?'  
  ] },
  { video: video, phases: [
    'ÎïåÎ°úÎäî Îã§Ìà¨Í≥†, ÏõÉÍ≥†, Ïö∏Í≥† Í∑∏Îû¨ÏßÄÎßå Í∑∏Í≤ÉÏù¥',
    'Ïö∞Î¶¨Î•º Îçî Îã®Îã®ÌïòÍ≤å ÎßåÎì§Í≥†, ÏÑúÎ°úÎ•º ÎßûÏ∂∞Í∞ÄÎäî Î≤ïÏùÑ',
    'ÏïåÍ≤å Îê† Ïàò ÏûàÏóàÎçò Í≤É Í∞ôÏïÑ.'
  ] },
  { img: img3, phases: [
    'ÎöúÎãàÎäî Î∞∞Î†§Ïã¨Ïù¥ ÎßéÏùÄ ÏÇ¨ÎûåÏù¥Ïñ¥ÏÑú \n ÎàÑÍµ¨Î≥¥Îã§ ÎÇòÎ•º ÏÉùÍ∞ÅÌï¥Ï£ºÍ≥†',
    'ÎÇ¥Í∞Ä Í∞êÏ†ï Í∏∞Î≥µÏù¥ Ïã¨Ìï¥ÎèÑ ÎßûÏ∂∞Ï§òÏÑú \n Ìï≠ÏÉÅ Í≥†ÎßôÍ≥† ÎØ∏ÏïàÌï¥'
  ] },
  { video: video2, phases: [
    'Ìï≠ÏÉÅ ÎÇòÎ°ú Í∞ÄÎìùÏ∞® ÏûàÎã§ÎäîÍ≤ÉÏùÑ Î≥¥Ïó¨ Ï£ºÍ≥† \n ÌëúÌòÑÎèÑ ÎßéÏù¥ Ìï¥Ï§òÏÑú Í≥†ÎßàÏõå',
    'Ïó≠Ïãú Í∞ÄÏû• ÏûòÌïú ÏÑ†ÌÉùÏùÄ ÎöúÎãàÎ•º ÏÇ¨ÎûëÌïú ÏùºÏù¥Ïïº ‚ô•'
  ] }, 
  { video: video3, phases: [
    'Ïö∞Î¶¨Í∞Ä Í∞ôÏù¥ ÏÇ¥Î©¥ÏÑú \n Í∞ôÏù¥ ÏûàÎã§Îäî ÌñâÎ≥µÍ∞êÏù¥ Î¨¥ÏóáÏù∏ÏßÄ',
    'ÏÇ¨ÎûëÌïòÎäî ÏÇ¨ÎûåÍ≥º Ìï®ÍªòÌïúÎã§Îäî Ï†êÏù¥ ÏñºÎßàÎÇò \n ÌñâÎ≥µÌïú Í≤ÉÏù∏ÏßÄ',
    'Íπ®Îã¨ÏïòÏñ¥'
  ] },
  { img: img4, phases: [
    'Í∑∏Îü¥ÏàòÎ°ù Ï†êÏ†ê ÌôïÏã†Ïù¥ Îì§Í∏∞ ÏãúÏûëÌñàÍ≥†',
    'ÎöúÎãàÏôÄ ÌèâÏÉù Ìï®ÍªòÌïòÎ©∞, \n ÎöúÎãàÏôÄ Ïù¥ ÌñâÎ≥µÏùÑ Í∞ôÏù¥ÌïòÍ≥† Ïã∂Í≤å ÎêêÏñ¥'
  ] }, 
]

const currentIndex = ref(0)
const currentPhaseIndex = ref(0)
const typedText = ref('')
const currentItem = ref(items[0])
const showNextLink = ref(false)

function typeText(text, callback) {
  typedText.value = ''
  let i = 0
  const interval = setInterval(() => {
    typedText.value += text[i]
    i++
    if (i >= text.length) {
      clearInterval(interval)
      setTimeout(() => {
        callback?.()
      }, PHASE_DISPLAY_TIME)
    }
  }, TYPING_SPEED)
}

function playPhase() {
  const item = items[currentIndex.value]
  const phase = item.phases[currentPhaseIndex.value]
  setTimeout(() => {
    typeText(phase, () => {
      currentPhaseIndex.value++
      if (currentPhaseIndex.value < item.phases.length) {
        playPhase() // Îã§Ïùå phase ÏßÑÌñâ
      } else {
        // phase ÎÅùÎÇ¨ÏúºÎ©¥ Îã§Ïùå Ïù¥ÎØ∏ÏßÄÎ°ú Ï†ÑÌôò
        currentPhaseIndex.value = 0
        currentIndex.value++
        if (currentIndex.value < items.length) {
          typedText.value = ''
          currentItem.value = null
          setTimeout(() => {
            currentItem.value = items[currentIndex.value]
            playPhase()
          }, IMAGE_CHANGE_DELAY)
        } else {
          showNextLink.value = true
        }
      }
    })
  }, TYPING_DELAY)
}

onMounted(() => {
  currentItem.value = items[0]
  playPhase()
  new Sakura('#bbdoy')
})
</script>

